import React, { PureComponent, Fragment } from 'react';
import { Link, withRouter } from 'react-router-dom';
import { Table, Button, Popconfirm } from 'antd';
import {actionCreators} from "../store";
import { connect } from "react-redux";

class Config extends PureComponent {

  render() {
    const { rules, chapterCount, book, handleDeleteItem } = this.props;
    const columns = [
      {
        key: 'price',
        dataIndex: 'price',
        title: '金额',
        align: 'center'
      }, {
        key: 'start_index',
        dataIndex: 'start_index',
        title: '起始章节',
        align: 'center'
      },{
        key: 'end_index',
        dataIndex: 'end_index',
        title: '结束章节',
        align: 'center'
      },{
        key: 'action',
        title: 'Action',
        align: 'center',
        render(item, record, index) {
          return (
            <Popconfirm title="确定删除这条内容吗?" onConfirm={() => handleDeleteItem(record, index)} okText="Yes" cancelText="No">
              <Button size='small' type='danger'>Delete</Button>
            </Popconfirm>
          )
        }
      }
    ];
    return (
      <Fragment>
        <span style={{float: 'right', marginRight: '30px'}}>共 {chapterCount} 章</span>
        <Button style={{margin: '0 30px 30px 0'}} type='primary'>
          <Link to={'/build/novel/config/create?id=' + book.id + '&title=' + book.title}>创建规则</Link>
        </Button>
        <Button onClick={() => this.props.history.goBack()}>返回上一级</Button>
        <Table bordered rowKey={record => record._id} columns={columns} dataSource={rules}  pagination={false} />
      </Fragment>
    )
  }

  componentDidMount() {
    let record = decodeURI(this.props.location.search);
    let id = record.split('=')[1].split('&')[0];
    let title = record.split('=')[2];
    this.props.setBookInfo(id, title);
    this.props.getBookRules(id);
  }

}

const mapState = (state) => ({
  book: state.novel.book,
  rules: state.novel.rules,
  chapterCount: state.novel.chapterCount
});

const mapDispatch = (dispatch) => ({
  setBookInfo(id, title) {
    dispatch(actionCreators.setBookInfo(id, title))
  },
  getBookRules(id) {
    dispatch(actionCreators.getBookRules(id))
  },
  handleDeleteItem(record, index) {
    dispatch(actionCreators.handleDeleteItem(record, index))
  }
});

export default connect(mapState, mapDispatch)(withRouter(Config));